body {
  margin: 0;
  padding: 0;
  font-family:'Signika', sans-serif, source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}

ul {
list-style-type: none;
}

code {
  font-family:'Signika', sans-serif, source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

span, b {
    display: inline-block;
    height: 1.5em;
    vertical-align: middle;
}

table {
    display: inline-block;
    border-collapse: separate;
    border-spacing: 2px;
    border-color: grey;
}

form {
    display: inline-block;
    vertical-align: middle;
}
tooltip {
  display: inline-block;
  vertical-align: middle;
  text-align: center;
}
